.tabbar {
  position: fixed;
  display: flex;
  justify-content: space-around;
  bottom: 0px;
  left: 0px;
  width: 100vw;
  box-shadow: 0px -1px 10px rgb(222, 222, 222);
  padding-bottom: 10px;
  z-index: 0;
}

.item span {
  flex: 1;
  flex-grow: 0;
  height: 100%;
  font-size: 1rem !important;
}
